<template>
<transition name="fade" mode="out-in">
  <div v-if="listRender" class="row component-section">
    <div class="col-md-10 col-md-push-1 component-box">
      <div class="component-box">
        <!-- table card example -->
        <paginate-links for="paginateReturn" :async="true" :step-links="{
                                  prev: 'Back',
                                  next: 'Next'
                                }" :hide-single-page="true" :show-step-links="true" :classes="{
                                'ul': ['pagination', 'table-pagination'],
                                '.next > a': 'next-link',
                                '.prev > a': 'prev-link'
                              }">
        </paginate-links>
        <div class="table-responsive pmd-card pmd-z-depth">
          <paginate name="paginateReturn" :list="searchReturn" :per="10" tag="table" class="table table-mc-red pmd-table">
            <!-- <table class="table table-mc-red pmd-table"> -->
            <thead>
              <tr>
                <th>Actions</th>
                <th>Artist - Title</th>
                <th>Single UPC</th>
                <th>Year</th>
                <th>Format</th>
              </tr>
            </thead>
            <tbody v-for="(listing, index) in paginated('paginateReturn')">
              <tr>
                <td class="pmd-table-row-action">
                  <a class="btn pmd-btn-fab pmd-btn-flat pmd-ripple-effect btn-default btn-sm child-table-expand direct-expand child-table-collapse" @click="switchOpen"><i class="material-icons md-dark pmd-sm"></i></a>
                  <a class="btn pmd-btn-fab pmd-btn-flat pmd-ripple-effect btn-default btn-sm" @click="remove(index, $event)">
                                                  <i class="material-icons md-dark pmd-sm">delete</i>
                                              </a>
                </td>
                <td><a class="fat-finger" :href="'https://www.discogs.com' + listing.uri" target="top">{{listing.title}}</a></td>
                <td>{{listing.singleUpc}}</td>
                <td>{{listing.year}}</td>
                <td>{{listing.singleFormat}}</td>
              </tr>
              <tr class="child-table">
                <td colspan="12">
                  <div class="direct-child-table">
                    <table class="table pmd-table table-striped table-sm">
                      <thead>
                        <tr>
                          <th></th>
                          <th>Label</th>
                          <th class="text-right">Other UPC's</th>
                          <th class="text-right">ID</th>
                          <th class="text-right">CatNo</th>
                          <th>Other Formats</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td class="really-no-padding">
                            <a :href="'https://www.discogs.com' + listing.uri" target="top"><img :src="listing.thumb"/></a>
                          </td>
                          <td>
                            <ul class="really-no-padding">
                              <li v-for="label in listing.label" class="no-padding">
                                {{label}}
                              </li>
                            </ul>
                          </td>
                          <td class="no-padding">
                            <ul class="really-no-padding">
                              <li v-for="upc in listing.barcode" class="text-right">
                                {{upc}}
                              </li>
                            </ul>
                          </td>
                          <td class="text-right">{{listing.id}}</td>
                          <td class="text-right">{{listing.catno}}</td>
                          <td class="no-padding">
                            <ul>
                              <li v-for="format in listing.format" class="text-right">
                                {{format}}
                              </li>
                            </ul>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </td>
              </tr>
            </tbody>
          </paginate>
          <!-- </table> -->
        </div>
      </div>
    </div>
  </div>
</transition>
</template>

<script>
import {
  mapGetters
} from 'vuex';
import {
  mapState
} from 'vuex';
export default {
  data() {
    return {
      paginate: ['paginateReturn']
    }
  },
  computed: {
    ...mapGetters([
      'searchReturn'
    ]),
    ...mapState([
      'listRender'
    ])
  },
  methods: {
    remove(index, e) {
      this.searchReturn.splice(index, 1);
      var target = e.target;
      if (!target.matches('a')) {
        var $parent = target.parentNode;
        $($parent).siblings().removeClass("child-table-collapse");
      } else {
        $(target).siblings().removeClass("child-table-collapse");
      }
      var $parentTR = $(target).closest('tr');
      var $uncleTR = $($parentTR).siblings('tr');
      var $childTable = $($uncleTR).find('.direct-child-table');
      if ($($childTable).hasClass('showMe')) {
        $($childTable).removeClass('showMe').slideToggle(200);
      }
    },
    switchOpen(e) {
      var target = e.target;
      if (!target.matches('a')) {
        var $parent = target.parentNode;
        $($parent).toggleClass("child-table-collapse");
      } else {
        $(target).toggleClass("child-table-collapse");
      }
      var $parentTR = $(target).closest('tr');
      var $uncleTR = $($parentTR).siblings('tr');
      var $childTable = $($uncleTR).find('.direct-child-table');
      $($childTable).addClass('showMe').slideToggle(200);
    }
  }
}
</script>



