<template>
<transition name="fade" mode="out-in">
  <div v-if="listRender" class="row component-section">
    <div class="col-md-10 col-md-push-1 col-lg-12 col-lg-push-0 component-box">
      <!-- table card example -->
      <paginate-links for="paginateReturn" :async="true" :step-links="{
                                  prev: 'Back',
                                  next: 'Next'
                                }" :hide-single-page="true" :show-step-links="true" :classes="{
                                'ul': ['pagination','card-pagination'],
                                '.next > a': 'next-link',
                                '.prev > a': 'prev-link'
                              }">
      </paginate-links>
      <!-- Default card starts -->
      <paginate name="paginateReturn" :list="searchReturn" :per="24" tag="div" class="row masonry">
        <return-card v-for="(listing, key, index) in paginated('paginateReturn')" :listing="listing" :index="index" :key="key"></return-card>
      </paginate>
    </div>
  </div>
</transition>
</template>

<script>
import {
  mapGetters
} from 'vuex';
import {
  mapState
} from 'vuex';
import ReturnCard from './ReturnCard.vue'
export default {
  data() {
    return {
      paginate: ['paginateReturn']
    }
  },
  computed: {
    ...mapState([
      'listRender'
    ]),
    ...mapGetters([
      'searchReturn'
    ])
  },
  components: {
    returnCard: ReturnCard
  },
  updated: function() {
    this.$nextTick(function() {
      $('.pmd-tabs').pmdTab();
    });
  }
}
</script>
<style lang="scss" >
.panel-footer {
    padding: 0;
    .pmd-card-actions {
        padding: 4px;
    }
}
.row.masonry {
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    .masonry-brick {
        margin-bottom: 20px;
    }
    .pmd-card {
        //height: 100%;
        margin-bottom: 0;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        .pmd-card-actions {
            span {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                * {
                    margin: 0;
                }
            }
        }
    }
}
.pmd-card-media-inline .pmd-card-media h2.pmd-card-title-text {
    font-size: 1.1rem;
    margin-top: 16px;
}

.pmd-card-body {
    padding: 0 !important;
    margin: 0 !important;
    min-height: 0 !important;
    img {
        height: 180px;
        max-width: 180px;
        padding: 16px;
        object-fit: contain;
        object-position: left top;
    }
    .tab-pane {
        height: 180px;
        &.pane-list {
            margin: 0;
            overflow-y: auto;
            .list-group-item {
                position: relative;
                display: block;
                padding: 10px 15px;
                margin-bottom: -1px;
                background-color: #fff;
                border: none;
                border-top: 1px solid #ddd;
                border-bottom: 1px solid #ddd;
            }
        }
    }
    .tab-pane:first-of-type {
        margin: 0;
    }

    &.card-flex-override {
        height: 100%;
        > div {
            height: 100%;
        }
    }
}
ul {
    padding: 0;
    list-style: none;
}
.pmd-card-subtitle-text {
    display: block;
}
.media-body {
    .pmd-card-subtitle-text:last-child {
        margin-bottom: 16px;
    }
}
.pmd-tabs {
    border-top-right-radius: 3px;
    border-top-left-radius: 3px;
    overflow: auto;
    &::-webkit-scrollbar {
        display: none;
    }
    .pmd-tab-active-bar {
        ::-webkit-scrollbar {
            display: none;
        }
    }
}
</style>



