.flex-container-content {
    display: flex;
    flex-direction: column;
    height: 100vh;
    #content {
        padding-bottom: 0;
    }
    @media screen and (max-width: 767px) {
        footer {
            margin: 0;
        }
    }
}

.pmd-sidebar-overlay {
    transition: opacity 0.2s ease-in;
}

@media (min-width: 768px) {
    .pmd-sidebar-overlay.pmd-sidebar-overlay-active {
        visibility: visible;
        opacity: 0.5;
    }
}



/*TRANSITION ANIMATIONS*/

.fade-enter,
.fade-leave-to {
    /* set tranisition styles on enter and leave-to*/
    opacity: 0;
}

.fade-enter {
    transform: scale(1.1);
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 300ms cubic-bezier(0.68, -0.1, 0.265, 1.55);
    /* set beziers on active classes */
}

.fade-enter,
.fade-leave-to {
    /* set tranisition styles on enter and leave-to*/
    opacity: 0;
}

.fade-enter {
    transform: scale(1.1);
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 300ms cubic-bezier(0.68, -0.1, 0.265, 1.55);
    /* set beziers on active classes */
}

.select-checkbox.checkbox.pmd-default-theme {
    margin: 0;
    cursor: pointer;
}

.table {
    margin-bottom: 0;
}

.table>tbody>tr>td {
    border: none;
}

.closeIcon {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.pagination {
    cursor: pointer;
    display: flex;
    flex-flow: row wrap;
    .active>a,
    .active>a:hover {
        background-color: #4285f4;
        border-color: #4285f4;
    }
    &.card-pagination {}
    &.table-pagination {
        margin-bottom: 0;
        li {
            cursor: pointer;
            &:last-child {
                a {
                    border-bottom-right-radius: 0;
                }
            }
            &:first-child {
                a {
                    border-bottom-left-radius: 0;
                }
            }
        }
