<template>
<div>
  <app-search-form></app-search-form>
  <app-search-return-table v-if="viewToggle"></app-search-return-table>
  <app-search-return-cards v-else></app-search-return-cards>
</div>
</template>
<script>
import SearchReturnTable from './searchlistings/SearchReturnTable.vue'
import SearchReturnCards from './searchlistings/SearchReturnCards.vue'
import SearchForm from './searchlistings/SearchForm.vue'
import {
  mapState
} from 'vuex';
export default {
  computed: {
    ...mapState([
      'viewToggle'
    ])
  },
  components: {
    appSearchReturnTable: SearchReturnTable,
    appSearchReturnCards: SearchReturnCards,
    appSearchForm: SearchForm
  }
}
</script>



