<template>
  <div class="col-lg-4 col-md-6 col-sm-12 col-xs-12 masonry-brick">
    <!--Media, and Action area -->
    <div class="pmd-card pmd-card-media-inline pmd-z-depth">
      <div class="pmd-tabs pmd-tabs-bg">
        <div class="pmd-tab-active-bar"></div>
        <ul class="nav nav-tabs" role="tablist">
          <li role="presentation" class='active'>
            <a :href="'#' + listing.id" :aria-controls="listing.id" role="tab" data-toggle="tab">Title</a>
          </li>
          <li v-if="listing.barcode" role="presentation">
            <a :href="'#' + listing.id + '_UPC'" :aria-controls="listing.id + '_UPC'" role="tab" data-toggle="tab">UPC</a>
          </li>
          <li v-if="listing.label" role="presentation">
            <a :href="'#' + listing.id + '_Labels'" :aria-controls="listing.id + '_Labels'" role="tab" data-toggle="tab">Labels</a>
          </li>
          <li v-if="listing.format" role="presentation">
            <a :href="'#' + listing.id + '_Formats'" :aria-controls="listing.id + '_Labels'" role="tab" data-toggle="tab">Formats</a>
          </li>
        </ul>
      </div>
      <div class="pmd-card-body card-flex-override">
        <div class="tab-content">
          <div role="tabpanel" class="tab-pane active" :id="listing.id">
            <div class="pmd-card-media">
              <!-- Card media heading -->
              <div class="media-body">
                <a :href="'https://www.discogs.com' + listing.uri" target="top">
                  <h2 class="pmd-card-title-text">{{listing.title}}</h2>
                </a>
                <span v-if="listing.year" class="pmd-card-subtitle-text">Release Year: {{listing.year}}</span>
                <span v-if="listing.singleFormat" class="pmd-card-subtitle-text">Format: {{listing.singleFormat}}</span>
              </div>
              <!-- Card media image -->
              <div class="media-right media-top">
                <a :href="'https://www.discogs.com' + listing.uri" target="top"><img :src="listing.thumb" /></a>
              </div>
            </div>
          </div>
          <div v-if="listing.barcode" role="tabpanel" class="tab-pane pane-list" :id="listing.id + '_UPC'">
            <ul class="list-group">
              <li class="list-group-item">{{listing.singleUpc}}</li end-v-for>
              <li class="list-group-item" v-for="(upc, index, key) in listing.barcode" :key="index">{{upc}}</li end-v-for>
            </ul>
          </div>
          <div v-if="listing.label" role="tabpanel" class="tab-pane pane-list" :id="listing.id + '_Labels'">
            <ul class="list-group">
              <li class="list-group-item" v-for="(label, index, key) in listing.label" :key="index">{{label}}</li end-v-for>
            </ul>
          </div>
          <div v-if="listing.format" role="tabpanel" class="tab-pane pane-list" :id="listing.id + '_Formats'">
            <ul class="list-group">
              <li class="list-group-item" v-for="(format, index, key) in listing.format" :key="index">
                {{format}}
              </li end-v-for>
            </ul>
          </div>
        </div>
      </div>
      <div class="panel-footer">
        <div class="pmd-card-actions">
          <span>
            <div>
              Quantity: {{listing.quantity}}
            </div>
            <!-- <a :class="button" @click="remove(index, $event)">
                                  <i class="material-icons md-dark pmd-sm">delete</i>
                                </a> -->
            <a :class="button" :href="'https://www.discogs.com' + listing.uri" target="top" type="button">
              <i class="material-icons md-dark pmd-sm">open_in_new</i>
            </a>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  mapState
} from 'vuex';
import {
  mapActions
} from 'vuex';
export default {
  props: ['listing', 'index'],
  data() {
    return {
      button: 'btn pmd-btn-fab pmd-btn-flat pmd-ripple-effect btn-default btn-sm',
      ...mapState([
        'savedMusic'
      ])
    }
  },
  methods: {
    remove(index, e) {
      this.savedMusic.splice(index, 1);
      var target = e.target;
      if (!target.matches('a')) {
        var $parent = target.parentNode;
        $($parent).siblings().removeClass("child-table-collapse");
      } else {
        $(target).siblings().removeClass("child-table-collapse");
      }
      var $parentTR = $(target).closest('tr');
      var $uncleTR = $($parentTR).siblings('tr');
      var $childTable = $($uncleTR).find('.direct-child-table');
      if ($($childTable).hasClass('showMe')) {
        $($childTable).removeClass('showMe').slideToggle(200);
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.pmd-card-media {
  padding-right: 0;
  margin-top: 0;
}

.row.masonry .pmd-card .pmd-card-actions span {
  justify-content: space-between;
  padding-left: 16px;
}
</style>



