<template>
  <div class="row" id="card-masonry">
    <div class="col-md-10 col-md-push-1 col-sm-12 col-xs-12">
      <form v-on:submit.prevent="Ajax">
        <div class='form-group form-group-lg pmd-textfield pmd-textfield-floating-label col-md-4' :class="title.name ? ' pmd-textfield-floating-label-completed' : null">
          <label>Band</label>
          <input class='form-control' type='text' v-model="title.name" />
        </div>
        <div class='form-group form-group-lg pmd-textfield pmd-textfield-floating-label col-md-4' :class="title.album ? ' pmd-textfield-floating-label-completed' : null">
          <label>Album</label>
          <input class='form-control' type='text' v-model="title.album" />
        </div>
        <div class='form-group form-group-lg pmd-textfield pmd-textfield-floating-label col-md-4' :class="title.upc ? ' pmd-textfield-floating-label-completed' : null">
          <label>UPC</label>
          <input class='form-control' type='text' v-model="title.upc" />
        </div>
        <div class="col-sm-6 mobile-vert-spacer">
          <label class="radio-inline pmd-radio pmd-radio-ripple-effect">
            <input type="radio" name="optionsRadios" id="CD" value="CD" v-model="title.format">
            <span for="CD">CD</span>
          </label>
          <label class="radio-inline pmd-radio pmd-radio-ripple-effect">
            <input type="radio" name="optionsRadios" id="Vinyl" value="Vinyl" v-model="title.format">
            <span for="Vinyl">Vinyl</span>
          </label>
          <label class="radio-inline pmd-radio pmd-radio-ripple-effect">
            <input type="radio" name="optionsRadios" id="Cassette" value="Cassette" v-model="title.format">
            <span for="Cassette">Tape</span>
          </label>
          <label class="radio-inline pmd-radio pmd-radio-ripple-effect">
            <input type="radio" name="optionsRadios" id="any" value="" v-model="title.format">
            <span for="any">Any</span>
          </label>

        </div>
        <div class="col-sm-6   mobile-vert-spacer">
          <button class='pull-right pmd-ripple-effect btn pmd-btn-raised btn-danger' type="submit">Search</button>
          <button class='pull-right pmd-ripple-effect btn btn-default' @click.prevent="clearForm">Clear</button>
          <div class="pmd-switch">
            <label>
              <input type="checkbox" :value="viewToggle" @click="updateViewToggle" checked>
              <span class="pmd-switch-label"></span>
            </label>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import {
  mapActions
} from 'vuex';
import {
  mapState
} from 'vuex';
import {
  mapGetters
} from 'vuex';
import {
  mapMutations
} from 'vuex';
export default {
  computed: {
    ...mapState([
      'title',
      'perPage',
      'viewToggle'
    ])
  },
  methods: {
    ...mapActions([
      'Ajax'
    ]),
    ...mapMutations([
      'clearForm',
      'updateViewToggle'
    ]),
    updatePerPage(e) {
      this.$store.commit('updatePerPage', e.target.value);
    }
  }
}
</script>
<style scoped lang="scss">
.mobile-vert-spacer {
  margin-top: 2rem;
  margin-bottom: 2rem;
  @media (min-width: 768px) {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}

.pull-right {
  float: none !important;
  @media (min-width: 768px) {
    float: right !important;
  }
}

.pmd-switch {
  display: inline-block;
  top: 50%;
  margin-left: 1rem;
  transform: translateY(20%);
  @media screen and (min-width: 767px) {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>



