import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    viewToggle: false,
    musicSearch: [],
    savedMusic: [],
    requestMusic: "http://api.discogs.com/database/search?type=release&country=US",
    bandParam: "&artist=",
    albumParam: "&title=",
    searchQuery: "&q=",
    barcodeParam: "&barcode=",
    formatParam: "&format=",
    perPageparam: "&per_page=",
    perPage: "100",
    Oauth: "&key=COmwGBAqWkMRnFFmdiON&secret=vffgxgmpqRGbjVkRyJihMZBaOluuoHXY",
    fullQuery: "",
    toggle: true,
    listRender: false,
    title: {
      name: 'Led Zeppelin',
      album: 'III',
      upc: '',
      format: ''
    },
    query: 'Led Zeppelin III'
  },
  getters: {
    searchReturn: state => {
      return state.musicSearch;
    },
    inventoryReturn: state => {
      return state.savedMusic;
    }
  },
  mutations: {
    updateViewToggle(state) {
      state.viewToggle = !state.viewToggle;
    },
    clearForm(state) {
      state.musicSearch = [];
      state.listRender = false;
    },
    updateQuery(state, query) {
      state.query = query;
      if (query.length > 0) {
        state.title.name = "";
        state.title.album = "";
        state.title.upc = "";
        state.title.format = "";
      }
    },
    updatePerPage(state, perPage) {
      state.perPage = perPage;
    },
    makeUniversalSearch(state) {
      if (state.title.name.length > 0 || state.title.album.length > 0) {
        state.query = ""
      }
      const callParams = state.requestMusic + state.searchQuery + state.query + state.Oauth +
        state.albumParam + state.title.album + state.bandParam + state.title.name + state.barcodeParam + state.title.upc + state.formatParam + state.title.format + state.perPageparam + state.perPage;
      state.toggle = false;
      $.ajax({
        url: callParams,
        dataType: 'jsonp',
        error: function (response) {
          console.log(response);
        },
        success: function (response) {
          const searchReturn = response.data.results;
          const musicSearchArray = [];
          for (let key in searchReturn) {
            // use the unique keynames to access their user objects
            musicSearchArray.push(searchReturn[key]);
          }
          searchReturn.forEach((item) => {
            if (item.barcode) {
              const barCodeArray = item.barcode;
              item.singleUpc = barCodeArray.shift();
            }
            if (item.format) {
              const formatArray = item.format;
              item.singleFormat = formatArray.shift();
            }
          });
          state.musicSearch = musicSearchArray;
          state.toggle = true;
          if (musicSearchArray.length > 0) {
            state.listRender = true;
          } else {
            state.listRender = false;
          }
        }
      });
    },
    save(state, listing) {
      const record = state.savedMusic.find(element => {
        return element.id == listing.id;
      });
      if (record) {
        record.quantity++;
      } else {
        listing.quantity = 1;
        state.savedMusic.push(listing);
      }
      axios.put('https://pos-dash.firebaseio.com/data.json', state.savedMusic);
      console.log(state.savedMusic, listing);
    },
    getInventory(state) {
      axios.get('https://pos-dash.firebaseio.com/data.json').then((response) => {
        if (response.data) {
          console.log(response.data);
          state.savedMusic = response.data;
        } else {
          state.savedMusic = [];
        }
      });
    }
  },
  actions: {
    Ajax: ({
      commit
    }) => {
      commit('makeUniversalSearch');
    },
    getSavedInventory: ({
      commit
    }) => {
      commit('getInventory');
    },
    save: ({
      commit
    }, listing) => { //order is the payload from the stock.vue file
      commit('save', listing);
    }
  }
});


