	<template>
	<aside  :class="[windowWidth > 1200 ? 'pmd-sidebar-slide-push pmd-sidebar-open' : 'pmd-sidebar-left-fixed']" class="pmd-sidebar sidebar-default  pmd-sidebar-left  bg-fill-darkblue sidebar-with-icons" role="navigation">
		<ul class="nav pmd-sidebar-nav">
			<router-link  tag="li" v-for="(link, key) in links"
			class="dropdown pmd-dropdown pmd-user-info visible-xs visible-md visible-sm visible-lg" :key="key" :to="link.route">
				<a aria-expanded="false" data-toggle="dropdown" class="btn-user dropdown-toggle media" data-sidebar="true">
					<div class="media-left media-middle">
						<i class="fa" :class="link.fa" aria-hidden="true"></i>
					</div>
					<div class="media-body media-middle">{{ link.name }}</div>
					<div class="media-right media-middle"><i class="dic-more-vert dic"></i></div>
				</a>
			</router-link>

		</ul>
	</aside><!-- End Left sidebar -->
	<!-- Sidebar Ends -->
</template>

<script>
export default {
  data() {
    return {
      links: [{
          name: "User",
          route: '/',
          fa: "fa-2x fa-user-circle-o",
          sublinks: [{
            name: "Logout",
            route: '/',
            fa: "fa-sign-out"
          }]
        },
        {
          name: "Search",
          route: '/',
          fa: "fa-search"
        },
        {
          name: "Inventory",
          route: '/inventory',
          fa: "fa-list",
          sublinks: [{
              name: "Search",
              route: '/',
              fa: "fa-search",
              route: '/'
            },
            {
              name: "Edit",
              route: '/',
              fa: "fa-edit"
            },
            {
              name: "Add",
              route: '/',
              fa: "fa-plus"
            },
            {
              name: "Remove",
              route: '/',
              fa: "fa-minus"
            }
          ]
        }
        // {
        //   name: "Item History",
        //   route: '/',
        //   fa: "fa-history"
        // },
        // {
        //   name: "Transaction History",
        //   route: '/',
        //   fa: "fa-barcode"
        // },
        // {
        //   name: "Create PO",
        //   route: '/',
        //   fa: "fa-file-text"
        // }
      ],
      windowWidth: window.innerWidth
    }
  },
  methods: {
    handleWindowResize(event) {
      this.windowWidth = event.currentTarget.innerWidth;
    },
  },
  beforeDestroy: function() {
    window.removeEventListener('resize', this.handleWindowResize)
  },
  mounted() {
    window.addEventListener('resize', this.handleWindowResize);
  }
}
</script>

<style lang="scss" scoped>
.media-left {
    img {
        height: 40px;
        width: 40px;
        border-radius: 100%;
    }
}
</style>



