<template>
  <form class="navbar-form navbar-left" @submit.prevent="searchAndReturn" style="display:flex">
    <div class="form-group">
      <input type="text" class="form-control" placeholder="Search" required="true" v-model="query" @input="updateQuery">
    </div>
    <button class="pmd-ripple-effect btn btn-primary">Search</button>
  </form>
</template>

<script>
import {
  mapActions
} from 'vuex';
import {
  mapState
} from 'vuex';
export default {
  computed: {
    ...mapState([
      'query'
    ])
  },
  methods: {
    ...mapActions([
      'Ajax'
    ]),
    updateQuery(e) {
      console.log(e.target.value)
      this.$store.commit('updateQuery', e.target.value);
    },
    searchAndReturn() {
      this.$router.push('/');
      this.Ajax();
    }
  }
}
</script>

<style lang="scss" scoped>
.navbar-form {
  display: flex;
  border-color: transparent;
  border: none !important;
  box-shadow: none;
  margin-top: 4px;
  @media screen and (min-width: 768px) {
    margin-top: 8px;
  }
  .form-group {
    margin-bottom: 0;
    margin-right: 3px;
  }
  input {
    width: 0;
    min-width: 200px;
    transition: width 0.75s ease;
  }
  input:focus {
    @media screen and (min-width: 736px) {
      width: 300px;
      transition: width 0.25s ease;
    }
  }
}
</style>



